/* jshint esversion:6 */
/*
 * Game state
 * ==========
 *
 */

import Board from '../objects/Board';
import Emoji from '../objects/Emoji';
import Score from '../objects/Score';
import Clock from '../objects/Clock';

export default class Game extends Phaser.State {
  create() {
    this.stage.backgroundColor = '#1E70A2'; //'#2d2d2d';

    // TODO: Allow screen to shake on error
    //this.setupScreenQuake();
  
    this.strobe = this.game.add.tween(this.stage).to({backgroundColor: 0xFFFFFF}, 20, Phaser.Easing.Linear.None, false, 0, -1, true);

    // Sounds for the state
    this.sndGood = this.add.audio('choice-good');
    this.sndBad = this.add.audio('choice-bad');
    this.sndNewBoard = this.add.audio('level-newboard');

    // Create a group for the user info
    this.hud = this.add.group();

    // Display message
    var style = { font: '72px Arial', fill: '#fff', align: 'center'};
    this.findRequest = new Phaser.Text(this.game, this.world.centerX, this.world.height, 'Eyes down for hunting', style);
    this.findRequest.scale.setTo(this.game.scaleRatio, this.game.scaleRatio);
    this.findRequest.stroke = '#000000';
    this.findRequest.strokeThickness = 20;
    this.findRequest.anchor.set(0.5, 1);
    this.findRequest.resolution = 1; // does this do anything?
    
    // Show a clue to what needs to be found next
    this.findTile = new Emoji(this.game, this.world.centerX, this.findRequest.y, 64 * this.game.scaleRatio, 1082, false);
    this.findTile.anchor.set(0.5, 1);
    //this.findTile.tint = 0x696969;
    this.findTile.alignTo(this.findRequest, Phaser.TOP_CENTER, 0, 0);

    // Add a shadow behind the clue
    this.shadow = new Emoji(this.game, this.findTile.x, this.findTile.y, 64 * this.game.scaleRatio, this.findTile.frame, false);
    this.shadow.anchor.set(0.5, 1);
    this.shadow.tint = 0x000000;
    this.shadow.alpha = 0.6;
    this.shadow.alignIn(this.findTile, Phaser.BOTTOM_LEFT, 2, 4);

    // Draw a bottom bar area to contain fing icon and text
    var graphics = this.add.graphics(0, 0);
    //graphics.lineStyle(2, 0x2d2d2d, 1);
    graphics.beginFill(0x2d2d2d, 0.5);
    //graphics.drawRect(this.findTile.x - 5 - this.findTile.width/2, this.findTile.y - 5 - this.findTile.height, this.findTile.width + 10, this.findTile.height + 10);
    graphics.drawEllipse(this.world.width/2, this.world.height - (this.findTile.height/3), this.world.width/2.5, this.findTile.height * 1.8);
    
    this.hud.add(graphics);
    this.hud.add(this.shadow);
    this.hud.add(this.findTile);
    this.hud.add(this.findRequest);

    // Initalise Score
    this.score = new Score(this.game, 0);
    this.hud.add(this.score);

    // Setup countdown timer
    this.clock = new Clock(this.game, 30);
    this.clock.alarmSignal.add(this.gameOver, this);
    this.hud.add(this.clock);
    this.hud.add(this.clock.timeIcon);

    // Starting Level
    this.game.level = 0;
    this.hiddenEmoji = null;

    // Generate the board
    this.board = new Board(this.game);

    // Define a click handler anytime something on the board is tapped
    this.board.clickTileSignal.add(this.clicked, this, 0, this);

    // Show the player some tiles to tap!
    this.board.generatePile();

    if (this.board.placedTiles.length == 0) {
      console.log('Failed to generate a board of tiles. Cannot proceed.');
      // TODO: cancel game.
      this.state.start('GameOver');
    }

    this.levelUp();
    this.nextToFind();

    // this.info = this.add.text(16, 36, ' ');
    // this.info.font = "Courier";
    // this.info.fontSize = 14;
    // this.info.fill = "#ffffff";
    // this.info.lineSpacing = 4;
    // this.info.setShadow(2, 2);
    //this.info.scale.setTo(this.scaleRatio/2, this.scaleRatio/2);

    this.game.world.bringToTop(this.hud);
  }

  render() {
    //this.emojiTiles.forEach(this.debugBounds, this, false);
  }

  update() {
    //this.scaleinfo();
  }

  debugBounds(sp) {
    this.game.debug.spriteBounds(sp);
  }
  
  nextToFind() {
    // Pick something random from the current tile pile whilst avoiding the same item as last time
    var newFindRequest = this.currentFindRequest;
    while(this.currentFindRequest == newFindRequest) {
      newFindRequest = Phaser.ArrayUtils.getRandomItem(this.board.placedTiles).tile;
    }

    this.currentFindRequest = newFindRequest;

    // Update the picker message
    this.findTile.frame = this.currentFindRequest;
    this.shadow.frame = this.currentFindRequest;
    this.findRequest.text = 'Find a ' + this.board.getTileName(this.currentFindRequest);

    // Provide a hint at start of game
    if (this.game.level == 1 && this.score.getScore() == 0) {
      this.hiddenEmoji = this.board.emojiTiles.getFirst('name', this.currentFindRequest);
      this.hiddenEmoji.tween = this.game.add.tween(this.hiddenEmoji).to({angle: '-5'}, 700, Phaser.Easing.Quadratic.InOut, false).to({angle: '+10'}, 700, Phaser.Easing.Quadratic.InOut, true, 0, -1, true);

      // Point out the first item to click on
      var handyPointer = new Emoji(this.game, this.game.world.width + 96, this.game.world.height + 48, 48, 1120, false); //this.hiddenEmoji.y+(this.hiddenEmoji.height/2)
      handyPointer.anchor.set(0.5, 0);
      //handyPointer.rotation(180); TODO rotate hand if it's pointing at something in lower half of screen

      // TODO make hand fly in from off screen to point at emoji to click
      var handyTween = this.game.add.tween(handyPointer);
      handyTween.to({ y: this.hiddenEmoji.y+(this.hiddenEmoji.height/2), x: this.hiddenEmoji.x}, 1000 + Math.random() * 3000, Phaser.Easing.Bounce.In, true);
      handyTween.onComplete.add(this.handyPointTween);
      this.hud.add(handyPointer);
    }
  }

  handyPointTween(handyPointer, handyTween) {
    handyTween.to({y: handyPointer.y-5}, 500, Phaser.Easing.Linear.InOut, true, 0, -1, true);
  }

  clicked(sprite) {
    sprite.tapped();

    // If requested emoji is clicked - deal with it
    if(this.currentFindRequest == sprite.name) {
      this.sndGood.play();

      // TODO: emitter https://phaser.io/examples/v2/particles/click-burst
      // https://phaser.io/examples/v2/particlestorm/confetti

      // Change the pile on every click/tap
      if (this.game.level > 2) {
        this.board.generatePile();
      }
    
      if (this.score.getScore() > 0) {
        this.clock.addTime(1); // free time bonus
      } else {
        // Remove helper hand
        var handyPointer = this.hud.getFirst('name', 1120); // FIXME: remove pointer hand
        if (handyPointer) {
          this.hiddenEmoji.tween.stop();
          handyPointer.destroy();
        }
      }

      var points = 15; //Math.floor(sprite.height * 0.08);
      this.score.addPoints(points);

      this.nextToFind();
      this.levelUp(this.score.getScore());
    } else {
      // Shake screen for incorrect tap
      this.sndBad.play();
      this.camera.x = 0;
      this.add.tween(this.camera).to({x: this.camera.x - 50}, 50, Phaser.Easing.Bounce.InOut, true, 0, 4, true).start();
      this.score.addPoints(-5);
    }

    this.clock.startCounter();
  }

  levelUp(currentScore = 0) {
    var level = Math.floor(currentScore/100)+1;

    // Do we level up?
    if(level != this.game.level) {
      this.game.level = level;
      this.sndNewBoard.play();

      switch(this.game.level) {
      case 1:
        //nothing
        break;

      case 2:
        this.board.emojiSpin();
        //this.nudgeBoard.start();
        break;
  
      case 3:
        // Throb
        //this.tween = this.game.add.tween(this.scale).to({x: this.scale.x/1.5, y: this.scale.y/1.5}, 500, Phaser.Easing.Sinusoidal.Out, true, 0, -1, true); // FIXME: this reset the sprite scale :(
        this.board.emojiTint(0x00dd00); // green
        this.board.emojiSpin(false);
        //this.nudgeBoard.start();
        break;
  
      case 4:
        // this.generatePile();
        // this.nextToFind();
        
        this.board.rotateBoard.start();
        break;

      case 5:
        this.board.emojiTint(0xff0505); // red
        this.board.emojiTiles.callAll('spinEmoji');
        break;

      case 7:
        this.board.emojiTiles.callAll('spinEmoji');
        // all 3 colours in groups of 5.
        break;

      case 8:
        this.board.rotateBoard.stop();
        this.board.emojiTint(0x0a0aee); // blue
        this.board.emojiTiles.callAll('spinEmoji', null, false);
        break;

      case 10:
        this.board.emojiTint(0x000000); // black silouette 
        break;

      case 11:
        this.board.strobeBoard();
        break;

      case 12:
        //ACID
        //https://phaser.io/examples/v2/display/pixi-render-texture
        break;

      case 13:
        this.board.strobeBoard(false);
        this.board.rotateBoard.start();
        break;
      }
    }
  }

  gameOver() {
    this.game.state.start('GameOver', true, false, this.score.getScore());
  }

  setupScreenQuake() {
    // we need to add margin to the world, so the camera can move
    var margin = 50;
    // and set the world's bounds according to the given margin
    var x = -margin;
    var y = -margin;
    var w = this.world.width + margin * 2;
    var h = this.world.height + margin * 2;
    // it's not necessary to increase height, we do it to keep uniformity
    this.world.setBounds(x, y, w, h);
    
    // we make sure camera is at position (0,0)
    this.world.camera.position.set(0);
  }

  resize() {
    console.log('state world resize detected');
  }

  scaleinfo() {
    var s = 'Game size: ' + this.game.width + ' x ' + this.game.height + '\n';
    s = s.concat('Actual size: ' + this.scale.width + ' x ' + this.scale.height + '\n');
    s = s.concat('minWidth: ' + this.scale.minWidth + ' - minHeight: ' + this.scale.minHeight + '\n');
    s = s.concat('maxWidth: ' + this.scale.maxWidth + ' - maxHeight: ' + this.scale.maxHeight + '\n');
    s = s.concat('aspect ratio: ' + this.scale.aspectRatio + '\n');
    s = s.concat('parent is window: ' + this.scale.parentIsWindow + '\n');
    s = s.concat('bounds x: ' + this.scale.bounds.x + ' y: ' + this.scale.bounds.y + ' width:' + this.scale.bounds.width + ' height: ' + this.scale.bounds.height + '\n');
    this.info.text = s;
  }
}