/*
 * gameOver state
 * ==============
 *
 * End of game
 */

export default class gameOver extends Phaser.State {
  init(scored) {
    this.scored = scored;
    this.sndTimeUp = this.add.audio('timeup');
  }

  create() {
    var style = { font: 'bold 128px Arial', fill: '#fff', align: 'center'};
    this.gameOver = this.add.text(this.world.centerX, -50, 'TIMES UP\n\nSCORE ' + this.scored, style);
    this.gameOver.scale.setTo(this.game.scaleRatio, this.game.scaleRatio);
    this.gameOver.anchor.setTo(0.5, 0.5);
    this.gameOver.setShadow(3, 3, 'rgba(0,0,0,0.5)', 2);

    // Drop the message to the centre of screen
    this.add.tween(this.gameOver).to( { y: this.world.centerY }, 2400, Phaser.Easing.Bounce.Out, true);
    
    this.sndTimeUp.play();
    this.time.events.add(Phaser.Timer.SECOND * 2, this.tapToPlay, this);
  }

  tapToPlay() {
    // Wait for a tap anywhere
    this.input.onTap.add(this.startAgain, this);
  }

  startAgain() {
    this.state.start('Game');
  }
}
