/* jshint esversion:6 */
/*
 * Logo
 * ====
 *
 * A sample prefab (extended game object class), displaying a spinning Phaser
 * logo.
 */

export default class Emoji extends Phaser.Sprite {
  constructor(game, x, y, radius, icon, rotate = true) {
    super(game, x, y, 'emojis', icon);
    this.scale.setTo(game.scaleRatio, game.scaleRatio);

    this.anchor.set(0.5, 0.5);
    this.setSize(radius);

    this.name = icon;
    this.inputEnabled = true;

    this.spinTween = this.game.add.tween(this).to({rotation: 360}, 190000, Phaser.Easing.Linear.None, false, 0, -1, false);
    this.spinTween.onComplete.add(this.spinEmojiEnd, this);
    this.tapTween = this.game.add.tween(this.scale).to({x: this.scale.x/1.5, y: this.scale.y/1.5}, 100, 'Sine.easeInOut', false, 0, 0, true);

    if(rotate) {
      // Make Emojis appear slowly at the start and pop out
      this.game.add.tween(this.scale).from({x: 0, y: 0}, 100, 'Sine.easeOut', true, 150 * Phaser.Math.random(1,5), 0, false);

      // Start off each at a random rotation
      this.angle = Phaser.Math.between(0, 360);
    }

    // @Debug outlines
    // var graphics = game.add.graphics(0, 0);
    // graphics.lineStyle(4, 0xff00ff, 0.7);
    // graphics.drawCircle(this.x, this.y, radius * 2);
    // graphics.lineStyle(1, 0xffffff, 0.7);
    // graphics.drawCircle(this.x, this.y, 10);
  }

  setSize(r) {
    r *= 2;
    this.width = r;
    this.height = r;
  }

  tapped() {
    this.tapTween.start();
  }

  spinEmoji(start = true) {
    if(start) {
      this.spinTween.start();
    } else {
      this.spinTween.pause();
    }
  }

  spinEmojiEnd() {
    this.game.add.tween(this).to({angle: this.angle + 180}, 5000, Phaser.Easing.Cubic.Out, true, 0, 0, false);
  }
}
