/* jshint esversion:6 */
/*
 * Score
 * =====
 *
 * Keeping count
 */

export default class Score extends Phaser.Text {

  constructor(game, startScore = 0) {
    super(game, 0, 0);
    this.scale.setTo(game.scaleRatio, game.scaleRatio);
    this.resolution = 1; // Retina

    this.score = startScore;

    this.setStyle({
      font: '128px Courier',
      fill: '#ffffff',
      align: 'left',
    });

    this.addPoints(0);
  }

  addPoints(points) {

    if (points > 0) {
      // Fade out the points addition
      this.pointsAdd = this.game.add.text(this.x + (120 * this.game.scaleRatio), this.y + (150 * this.game.scaleRatio), '+' + points,
        { font: '100px Courier', fill: '#ffffff', align: 'right'}
      );
      this.pointsAdd.scale.setTo(this.game.scaleRatio, this.game.scaleRatio);
      this.game.add.tween(this.pointsAdd).to({y: -20, alpha: 0}, 1000, Phaser.Easing.Cubic.In, true, 100, 0, false);
    } else if (this.score > 0) {
      // Take away points
      this.pointsRemove = this.game.add.text(this.x + (150 * this.game.scaleRatio), this.y + (80 * this.game.scaleRatio), points,
        { font: '100px Courier', fill: '#ff0000', align: 'right'}
      );
      this.pointsRemove.scale.setTo(this.game.scaleRatio, this.game.scaleRatio);
      this.game.add.tween(this.pointsRemove).to({y: +60, alpha: 0}, 1000, Phaser.Easing.Cubic.Out, true, 100, 0, false);

    }

    // Update score and display
    this.score += points;
    this.score = this.score <= 0 ? 0 : this.score;
    this.setText(Phaser.Utils.pad(this.score, 4, '0', 1));
  }

  getScore() {
    return this.score;
  }
}
