/* jshint esversion:6 */
/*
 * Clock
 * =====
 *
 * Keeping time
 */

export default class Clock extends Phaser.Text {

  constructor(game, countStart = 30) {
    super(game, game.world.width, 0, '000');
    this.scale.setTo(game.scaleRatio, game.scaleRatio);
    this.anchor.set(1, 0);
    this.setStyle({
      font: '128px Courier',
      fill: '#ffffff',
      align: 'right',
    });

    // Initialise countdown sound
    this.sndTickTock = game.add.audio('ticktock');

    // Show a little clock icon next to timer countdown
    this.timeIcon = game.add.sprite(0, 0, 'emojis', 1743);
    this.timeIcon.height = this.timeIcon.width = 32;
    this.timeIcon.scale.setTo(this.game.scaleRatio, this.game.scaleRatio);
    this.timeIcon.anchor.set(0.5, 0.5);
    this.timeIcon.alignTo(this, Phaser.LEFT_CENTER, 0, 2);

    // Animation for shaking the clock
    this.timeLowTween = this.game.add.tween(this.timeIcon).to({angle: '-20'}, 300, Phaser.Easing.Quadratic.InOut, false).to({angle: '+40'}, 300, Phaser.Easing.Quadratic.InOut, false, 0, -1, true);

    // Timer starting value
    this.counter = countStart;
    this.displayTime();
    
    //  Create our Timer
    this.timer = game.time.create(false);
    this.timer.loop(Phaser.Timer.SECOND, this.updateCounter, this);

    // Initalise alarm signal
    this.alarmSignal = new Phaser.Signal();
  }

  startCounter() {
    // TODO: check if event already exists
    this.timer.start();
  }

  updateCounter() {
    this.counter--;

    // Is it game over time?
    if (this.counter < 1) {
      this.sndTickTock.stop();
      this.alarmSignal.dispatch();
    } else if (this.counter == 10) {
      // Time is getting low!
      this.timeIcon.tint = 0xFF0000;
      this.timeLowTween.start();
      this.sndTickTock.loopFull();
    }

    this.displayTime();
  }

  addTime(extraSeconds) {
    this.counter += extraSeconds;
    this.displayTime();

    this.timeAdd = this.game.add.text(this.x - (120 * this.game.scaleRatio), this.y + (150 * this.game.scaleRatio), '+' + extraSeconds,
        { font: '100px Courier', fill: '#ffffff', align: 'right'}
    );
    this.timeAdd.scale.setTo(this.game.scaleRatio, this.game.scaleRatio);
    this.game.add.tween(this.timeAdd).to({y: -20, alpha: 0}, 1000, Phaser.Easing.Cubic.In, true, 0, 0, false);
  }

  displayTime() {
    this.setText(Phaser.Utils.pad(this.counter, 3, '0', 1));
  }
}
