/* jshint esversion:6 */
/*
 * Board
 * =====
 *
 */

import Emoji from '../objects/Emoji';

var TILE = [1188, 971, 1075, 797, 1081, 1082, 678, 643, 1407, 1405, 1675, 826, 492, 1287, 1115,756,670,794,708,665,622,536,321,498,455,240,197,154,112,68,84,204];
var TILENAME = [];

// Mixed colour items
TILENAME[68] = 'Cat';
TILENAME[498] = 'Monkey';
TILENAME[670] = 'Pig';

// Blue items
TILENAME[1081] = 'Bird';
TILENAME[1188] = 'Footprint';
TILENAME[197] = 'Whale';
TILENAME[204] = 'Book';

// White items
TILENAME[84] = 'Snowman';
TILENAME[112] = 'Panda';
TILENAME[1115] = 'Unicorn';
TILENAME[455] = 'Sheep';
TILENAME[1082] = 'Penguin';

// Grey items
TILENAME[756] = 'Elephant';
TILENAME[1287] = 'Shark';
TILENAME[1407] = 'Bell';
TILENAME[643] = 'Skull';

// Red items
TILENAME[321] = 'Mushroom';
TILENAME[536] = 'Melon';
TILENAME[1675] = 'Heart';
TILENAME[1075] = 'Fish';

// Yellow items
TILENAME[622] = 'Lemon';
TILENAME[665] = 'Banana';
TILENAME[708] = 'Pineapple';
TILENAME[492] = 'Star';
TILENAME[1405] = 'Padlock';
TILENAME[797] = 'Trophy';
TILENAME[678] = 'Fire';
TILENAME[971] = 'Bee';

// Green items
TILENAME[154] = 'Crocodile';
TILENAME[794] = 'Apple';
TILENAME[240] = 'Snail';
TILENAME[826] = 'Tree';

// TODO: hat can we use as a class instead of Sprite?
export default class Board extends Phaser.Sprite {

  constructor(game) {
    super(game);

    this.sndRotate = game.add.audio('level-rotate');

    // Create an initial board of tiles
    this.emojiTiles = game.add.group();

    // Normal coloured tiles to start with
    this.tintmode = 0xffffff;

    this.clickTileSignal = new Phaser.Signal();
  }

  getTileName(tileId) {
    return TILENAME[tileId];
  }

  clickCallback(sprite) {
    this.clickTileSignal.dispatch(sprite);
  }

  generatePile() {
    this.placedTiles = [];
    var sample = {};
    this.tilePile = Phaser.ArrayUtils.shuffle(TILE).slice(2, 22); // Attempt 20 tiles on the board

    // Define an area that tiles must fit on
    this.board = new Phaser.Ellipse(10, 80 * this.game.scaleRatio, this.game.world.width-20, this.game.world.height-(280 * this.game.scaleRatio));

    // Draw the board parimeter
    // var graphics = this.game.add.graphics(0, 0); // @debug
    // graphics.lineStyle(5, 0x2d2d2d, 1); // @debug
    // graphics.beginFill(0x2d2d2d, 0.5);
    // graphics.drawEllipse(this.board.x + (this.board.width/2),
    //   this.board.y + (this.board.height/2) ,
    //   this.board.width/2,
    //   this.board.height/2); // @debug

    // Group tiles for easy manipulation in levels
    this.emojiTiles.removeAll(true, true, false);

    // Set all tiles central pivot point for rotation of board
    this.emojiTiles.pivot = {x: this.game.world.centerX, y: this.game.world.centerY};
    this.emojiTiles.x = this.board.x + this.board.width/2;
    this.emojiTiles.y = this.board.y + this.board.height/2;

    // Define some common tween animations
    this.rotateBoard = this.game.add.tween(this.emojiTiles).to({rotation: 360}, 800000, Phaser.Easing.Linear.None, false, 0, -1, false);
    this.rotateBoard.onStart.add(this.rotateBoardSound, this);
    this.nudgeBoard = this.game.add.tween(this.emojiTiles).to({rotation: +45}, 1000, Phaser.Easing.Linear.None, false, 0, 0, false);

    // Display our pile of tiles inside the board
    this.stepcount = 1;
    this.tilePile.forEach(
      function(tilez) {
        sample = this.getBestCandidate(250);

        if (sample.radius != 0) {
          sample.tile = tilez;

          // Store new tile location
          this.placedTiles.push(sample);

          // Render tile on screen
          var t = new Emoji(this.game, sample.x, sample.y, sample.radius, tilez, true);
          t.events.onInputDown.add(this.clickCallback, this);
          this.emojiTiles.add(t);
        }
      },this);

    // Ensure any colour tint persists
    this.emojiTint();
  }

  getBestCandidate(k = 128) {
    var winningSample = new Phaser.Circle();

    // Remember the ellipse size of the full board
    var tr = {w: this.board.width, h: this.board.height, x: this.board.x, y: this.board.y};

    // Pick a radius for the tile
    var baseRadiusMin = 19, baseRadiusMax = this.game.world.width/4;
    var p = (this.stepcount - 1) / (this.tilePile.length - 1);
    this.stepcount+= 0.5;
    var np = 1 - p;
    var maxRadius = Math.floor(((baseRadiusMax - baseRadiusMin) * np) + baseRadiusMin);
    var margin = this.stepcount * 8;
    var minRadius = (maxRadius - margin) < baseRadiusMin ? baseRadiusMin : maxRadius - margin;

    var r = Phaser.Math.between(minRadius, maxRadius);
    var candidate = new Phaser.Circle(0, 0, r * 2);

    // Reduce board size to avoid a tile hanging outside of board
    this.board.height = tr.h - r;
    this.board.width = tr.w - r;
    this.board.x = this.board.x + (r/2);
    this.board.y = this.board.y + (r/2);

    // var graphics = this.add.graphics(0, 0); // @debug
    // graphics.lineStyle(3, 0xff0000, 0.7); // @debug
    // graphics.drawEllipse(this.board.x + (this.board.width/2),
    //   this.board.y + (this.board.height/2),
    //   this.board.width/2,
    //   this.board.height/2); // @debug

    for(var i = 0; i < k; i++){

      // Find a point within the defined circular board area
      this.erandom(candidate, this.board.x, this.board.y, this.board.width, this.board.height);

      // @Debug show attempted candidates
      // var graphics = this.add.graphics(0, 0); // @debug
      // graphics.lineStyle(1, 0xff0000, 0.3); // @debug
      // graphics.drawCircle(candidate.x, candidate.y, candidate.diameter);

      // First candidate then it's the best one!
      if(this.placedTiles.length == 0) {
        winningSample = candidate;
        break;
      }

      // Check if the candidate overlaps any existing tiles
      var clashed = false;
      clashed = this.clashTest(candidate);
      if(!clashed) {
        winningSample = candidate;
        break;
      }
    }

    // Reset game board radius for next round
    this.board.height = tr.h;
    this.board.width = tr.w;
    this.board.x = tr.x;
    this.board.y = tr.y;

    return winningSample;
  }

  erandom(out, x, y, width, height) {

    if (out === undefined) { out = new Phaser.Point(); }

    var p = Math.random() * Math.PI * 2;
    var r = Math.random();

    out.x = Math.sqrt(r) * Math.cos(p);
    out.y = Math.sqrt(r) * Math.sin(p);

    out.x = x + width/2 + (out.x * width / 2.0);
    out.y = y + height/2 + (out.y * height / 2.0);

    return out;
  }

  clashTest(tileCandidate) {
    var otherSample;
    var clash = false;

    for(var i = 0; i < this.placedTiles.length; i++) {
      // Copy the sample data to a simple object for comparison checks only
      otherSample = {x: this.placedTiles[i].x, y: this.placedTiles[i].y, width: this.placedTiles[i].width, height: this.placedTiles[i].height};
      
      // Add an additional margin around emojis
      otherSample.radius = this.placedTiles[i].radius + 2; 

      // Check if the emoji candidate and the existing samples don't overlap
      if(Phaser.Circle.intersects(tileCandidate, otherSample)) {
        clash = true;
        return clash;
      }
    }

    return false;
  }

  // Callback used by tween onStart
  rotateBoardSound() {
    this.sndRotate.play();
  }

  emojiSpin(start = true) {
    if(start) {
      this.emojiTiles.callAll('spinEmoji');
      this.sndRotate.play();
    } else {
      this.emojiTiles.callAll('spinEmoji', null, false);
    }
  }

  emojiTint(mode = this.tintmode) {
    this.emojiTiles.setAll('tint', mode);
    this.tintmode = mode;
  }

  strobeBoard(start = true) {
    if(start) {
      this.strobe.start();
    } else {
      this.strobe.pause();
    }
  }
}
