/* jshint esversion:6 */
/*
 * `app` module
 * ============
 *
 * Provides the game initialization routine.
 */

// Required: import the Babel runtime module.
import 'babel-polyfill';

// Import game states.
import * as states from './states';

export function init() {
  //const game = new Phaser.Game(window.innerWidth * window.devicePixelRatio, window.innerHeight * window.devicePixelRatio, Phaser.CANVAS);
  // const game = new Phaser.Game(414 * window.devicePixelRatio, 736 * window.devicePixelRatio, Phaser.CANVAS);
  const game = new Phaser.Game(376 * window.devicePixelRatio, 662 * window.devicePixelRatio, Phaser.CANVAS);

  game.scaleRatio = window.devicePixelRatio / 3;


  // Dynamically add all required game states.
  Object
    .entries(states)
    .forEach(([key, state]) => game.state.add(key, state));

  game.state.start('Boot');

  return game;
}
